/*=============== QUESTIONS ===============*/
.questions {
  background-color: var(--first-color-lighten);

  &__container {
    gap: 1.5rem;
    padding: 1.5rem 0;
  }
  &__group {
    display: grid;
    row-gap: 1.5rem;
  }
  &__item {
    background-color: var(--container-color);
    border-radius: 0.25rem;

    &:hover {
      box-shadow: 0 2px 8px hsla(var(--hue), 4%, 15%, 0.15);
    }
  }
  &__item-title {
    font-size: var(--small-font-size);
    font-weight: var(--font-medium);
  }
  &__icon {
    font-size: 1.25rem;
    color: var(--title-color);
  }
  &__description {
    font-size: var(--smaller-font-size);
    padding: 0px 15px;
  }
  &__header {
    display: flex;
    align-items: center;
    column-gap: 0.5rem;
    padding: 0.75rem 0.5rem;
    cursor: pointer;
  }
  &__content {
    overflow: hidden;
    height: 0;
  }
  &__item,
  &__header,
  &__item-title,
  &__icon,
  &__description,
  &__content {
    transition: 0.3s;
  }
}

/*Rotate icon and add font weight to titles*/
.accordion-open {
  & .questions__header,
  & .questions__content {
    background-color: var(--first-color);
  }
  & .questions__item-title,
  & .questions__description,
  & .questions__icon {
    color: #fff;
  }
  & .questions__icon {
    transform: rotate(45deg);
  }
}
